import React, { useState, useEffect } from 'react';
import axios from 'axios';
import ArtPlayer1 from './ArtPlayer1';
import './App.css';

function Vidstream({ selectedEpisodeId, selectedMovieId }) {
  const [video, setVideo] = useState('');
  const [qualityIndex, setQualityIndex] = useState(); // Default to auto quality
  const [subtitles, setSubtitles] = useState([]);
  const [fetchedUrl, setFetchedUrl] = useState([]);
  const [selectedSubtitleIndex, setSelectedSubtitleIndex] = useState(0);
  const [subtitlelang, setSubtitleLang] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(
          'https://api-shiori.vercel.app/movies/flixhq/watch',
          {
            params: {
              episodeId: selectedEpisodeId,
              mediaId: selectedMovieId,
            },
          }
        );

        const fetchedUrl = response.data.sources;
        setFetchedUrl(fetchedUrl);
        setQualityIndex(fetchedUrl.length - 1);

        const fetchedSubtitles = response.data.subtitles;
        setSubtitleLang(fetchedSubtitles.lang);

        if (fetchedUrl && fetchedUrl.length > 0) {
          const resultsarray = fetchedUrl[qualityIndex].url;
          setVideo(resultsarray);

          if (fetchedSubtitles && fetchedSubtitles.length > 0) {
            // Show only English subtitles
            const englishSubtitle = fetchedSubtitles.find(subtitle => subtitle.lang.includes("English"));
            if (englishSubtitle) {
              setSubtitles([englishSubtitle]);
              //console.log(englishSubtitle);
            } else {
              //console.log('English subtitles not found in the response.');
              setSubtitles([]);
            }
          } else {
           // console.log('Subtitles not found in the response.');
            setSubtitles([]);
          }
        } else {
         // console.log('Video URL not found in the response.');
        }
      } catch (err) {
       // console.log(err);
      }
    };

    // Only refetch if selectedEpisodeId has changed
    if (selectedEpisodeId !== null) {
      fetchData();
    }
  }, [selectedEpisodeId, selectedMovieId, qualityIndex]);

  const handleGetInstance = (artplayerInstance) => {
    // Check if artplayerInstance is not undefined before using it
    if (artplayerInstance && subtitles.length > 0) {
      //console.log(artplayerInstance);
      const selectedSubtitle = subtitles[selectedSubtitleIndex];
      // Now you can access subtitle property and use the switch method
      if (selectedSubtitle) {
      artplayerInstance.subtitle.switch(selectedSubtitle.url, {
        name: selectedSubtitle.lang,
      });
     }
    }
  };

  return (
    <div className='vidstream'>
      { (
        <>
          <div>
            <ArtPlayer1
          
              option={{
                url: video,
                autoSize: false,
                fullscreen: true,
                fullscreenWeb: true,
                setting: true,
                quality: fetchedUrl.map((item, index) => ({
                  default: index === qualityIndex,
                  html: ` ${item.quality}`,
                  url: item.url,
                })),
                settings: subtitles.length > 0 ?[
                  {
                    width: 200,
                    html: 'Subtitle',
                    tooltip: 'Show',
                    onSwitch: function (item) {
                      item.tooltip = item.switch ? 'Hide' : 'Show';
                      ArtPlayer1.subtitle.show = item.switch;
                      return item.switch;
                    },
                    selector: subtitles.map((subtitle, index) => ({
                      default: index === selectedSubtitleIndex,
                      html: subtitle.lang,
                      url: subtitle.url,
                    })),
                    onSelect: function (item) {
                      // Access artplayerInstance.subtitle.switch here if needed
                      return item.html;
                    },
                    subtitle: {
                      url:
                        subtitles.length > 0
                          ? subtitles[selectedSubtitleIndex].url
                          : '',
                      type: 'srt',
                      font: '20px sans-serif',
                      color: '#ffffff',
                      encoding: 'utf-8',
                    },
                  },
                ]:[],
              }}
              
              style={{
                
             
              }}
              className = "artplayer"
              getInstance={handleGetInstance}
            />
          </div>
        </>
      )}
    </div>
  );
}

export default Vidstream;
